@import "style";

// Used only in "Interface/DROPDOWNS"
.dropdown-menu-demo {
	position: static !important; 
	display: block !important;
}

// Used only in "Interface/DROPDOWNS" only in section "5" (5.01 and 5.02)!
.dropdown-menu-demo-customs {
	position: static !important; 
	display: flex !important;
}

// Used only in "Interface/MODALS"
.modal-demo > * {
	position: static !important; 
	display: block !important;
}

.form-group {
	&.required {
		.col-form-label {
			&:after {
				content: '*';
				color: red;
			}
		}
	}
}

.datepicker-12 {
	> div {
		width: 100% !important;
	}
}

@media print {
  .layout__sidebar {
    display: none;
  }
}
